// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'

import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
Vue.use(ElementUI);

// 
import '@/assets/css/reset.css'
// ,htmlfont-size
import '@/assets/js/resize.js'
Vue.config.productionTip = false

import createPersistedState from 'vuex-persistedstate'

import Vant from 'vant';
import 'vant/lib/index.css';


Vue.use(Vant);

import axios from 'axios'
import VueAxios from 'vue-axios'

Vue.use(VueAxios, axios);
import 'swiper/dist/css/swiper.css';

import "@/assets/resize.js"

import 'vant/lib/index.css';

Vue.use(Vant);

Vue.use(VueAxios, axios)


axios.interceptors.request.use(
    config => {
        // tokenhttpheadertoken
        // tokentoken
        config.withCredentials = true; // token ,

        return config;
    },
    error => {
        return Promise.error(error);
    })
Vue.use(VueAxios,axios);


//vuex
import VueX from "vuex";
import pModule from "./vuexModule/Apublic";
import ShopsInfor from './vuexModule/NearbyShopsInfor';
Vue.use(VueX);
const store = new VueX.Store({
    plugins: [createPersistedState()],
    modules: {
        p:pModule,
        shopsInfor:ShopsInfor,
    }
});

new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>',
  store
})



