<template>
    <div class="useCart">
        <!---->
        <div class="detail_top"><van-nav-bar title="" left-arrow @click-left="onClickLeft"></van-nav-bar></div>
        <!---->
        <p class="purchase">:<span>{{users.username}}</span></p>
        <!---->
        <div class="shurukuang">
            <input type="text" placeholder="10" maxlength='10' min="0" onkeyup="value=value.replace(/[^0-9]/g,'')" v-model="card_num" @input="judge">
            <input type="text" placeholder="6" maxlength='6' min="0" onkeyup="value=value.replace(/[^0-9]/g,'')" v-model="card_number" @input="judge">
        </div>
        <!---->
        <button @click="conversion" :disabled="isDis" class="conversion_one"></button>
        <!---->
        <p class="wenxts"></p>
        <!---->
        <div class="hint">
            <p></p>
            <p>30</p>
            <p>90</p>
            <p>360</p>
            <p>3431</p>
        </div>

        <!---->
        <transition enter-active-class="bounceIn 0.5s linear">
            <div v-show="show" class="tanchuang">
                <van-icon name="warning-o" />
                <br>
                <span>{{text}}</span>
                <button @click="show=!show"></button>
            </div>
        </transition>
    </div>
</template>

<script>
    export default {
        name: "UseCart",
        data(){
            return{
                card_num:"",
                card_number:"",
                //
                isDis:true,
                //
                show: false,
                //
                text:"",
            }
        },
        //
        methods: {
            onClickLeft() {
                this.$router.push({path: "/Vipcard"});
            },
            judge(){
                if (this.card_num.length==10&&this.card_number.length==6){
                    this.isDis=false;
                }else{
                    this.isDis=true;
                }
            },
            conversion(){
                this.show=true;
                this.text="";
            }
        },
        computed:{
            users(){
                return this.$store.state.p.ZYMpublic;
            }
        }
    }
</script>

<style scoped>
    .purchase{
        width: 100%;
        font-size: 1.5rem;
        padding: 1.5rem;
    }
    .purchase span{
        font-weight: bold;
    }
    .shurukuang input{
        width: 100%;
        height: 5rem;
        font-size: 1.5rem;
        padding-left: 1.5rem;
        background: white;
    }
    .useCart .conversion_one{
        width: 90%;
        margin-left: 5%;
        margin-top: 2rem;
        font-size: 1.5rem;
        font-weight: bold;
        line-height: 4rem;
        color: white;
        background: #4cd964;
        border: none;
        border-radius: 0.5rem;
    }
    .useCart button:disabled{
        background: #ccc;
    }
    /**/
    .wenxts{
        font-size: 1.5rem;
        line-height: 4rem;
        margin-top: 3rem;
        text-align: center;
        color: #999;
    }
    /**/
    .hint{
        width: 100%;
        padding: 0rem 3.5rem;
    }
    .hint p{
        font-size: 1.1rem;
        line-height: 1.7rem;
        color: #999;
    }
</style>


