<template>
    <div class="commend">
        <!---->
        <div class="detail_top"><van-nav-bar title="" left-arrow @click-left="onClickLeft"></van-nav-bar></div>
        <img src="../../static/imgs/activity.png" class="commend_img">
        <!---->
        <div class="van-row">
            <div class="van-col" @click="AppTan">
                <img src="data:image/png;base64,"><br>
                <span></span>
            </div>
            <div class="van-col" @click="AppTan">
                <img src="data:image/png;base64,"><br>
                <span>QQ</span>
            </div>
            <div class="van-col" @click="AppTan">
                <img src="data:image/png;base64,"><br>
                <span></span>
            </div>
        </div>
        <!--,-->
        <div class="earnings">
            <div class="earnings_left">
                <p></p>
                <p><span>0</span></p>
            </div>
            <div class="earnings_right pull-right">
                <p></p>
                <p><span>0</span></p>
            </div>
        </div>
        <!---->
        <p class="detail">--</p>
        <!---->
        <div class="invite">
            <img src="data:image/png;base64," alt=""><br>
            <span></span>
        </div>
        <!---->
        <transition enter-active-class="bounceIn 0.5s linear">
            <div v-show="show" class="tanchuang">
                <van-icon name="warning-o" />
                <br>
                <span>{{text}}</span>
                <button @click="show=!show"></button>
            </div>
        </transition>
    </div>
</template>

<script>
    export default {
        name: "Commend",
        data(){
            return{
                //
                show: false,
                //    
                text:"",
            }
        },
        methods: {
            onClickLeft() {
                this.$router.push({path: "/benefit"});
            },
            AppTan(){
                this.show=true;
                this.text="APP"
            }
        }
    }
</script>

<style scoped>
/**/
.commend_img{
    width: 100%;
}
/**/
.van-row{
    width: 100%;
    height: 13rem;
    background: white;
    margin-top: 0.5rem;
    text-align: center;
    font-size: 1.2rem;
    line-height: 2rem;
}
.van-row img{
    width: 6rem;
    margin-top: 2rem;
}
.van-col{
    height: 100%;
    width: 33.3%;
}
/*,*/
.earnings{
    width: 100%;
    margin-top: 2rem;
}
.earnings div{
    display: inline-block;
    width: 50%;
    text-align: center;
    font-size: 1.2rem;
    line-height: 2rem;
}
.earnings div:nth-child(1){
    border-right: 0.1rem solid #d9d9d9;
}
.earnings div span{
    font-size: 1.8rem;
    font-weight: bold;
}
.earnings div:nth-child(1) span{
    color: red;
}
/**/
.detail{
    font-size: 1.3rem;
    text-align: center;
    margin-top: 3rem;
}
/**/
.invite{
    width: 100%;
    text-align: center;
    margin-top: 2.8rem;
    font-size: 1.2rem;
    line-height: 2rem;
    color: #999;
}
.invite img{
    width: 8%;
}
</style>


