<template>
    <div class="">
        <!---->
        <div class="detail_top"><van-nav-bar title="" left-arrow @click-left="onClickLeft"></van-nav-bar></div>
        <!---->
        <div class="three">
            <div class="three_one">
                <input type="text" placeholder="" v-model="account">
            </div>
            <div class="three_one">
                <input :type="checked?'text':'password'" placeholder="" v-model="password">
                <van-switch v-model="checked" active-color="#4cd964" size="3rem"/>
                <span class="ming_close">abc</span>
            </div>
            <div class="three_one">
                <input type="text" placeholder="" v-model="verification">
                <img :src="imgUrl">
                <span class="imgurl_click" @click="changeImg">
                    <p></p>
                    <p class="changone"></p>
                </span>
            </div>
        </div>
        <!---->
        <div class="warm_prompt">
            <p></p>
            <p></p>
        </div>
        <!---->
        <div class="but">
            <button type="button" class="btn btn-lg btn-block" @click="judge"></button>
        </div>

        <!---->
        <router-link :to="{path: '/forget'}" class="chongzhi pull-right">?</router-link>
        <!---->
        <transition enter-active-class="bounceIn 0.5s linear">
            <div v-show="show" class="tanchuang">
                <van-icon name="warning-o" />
                <br>
                <span>{{text}}</span>
                <button @click="show=!show"></button>
            </div>
        </transition>
    </div>
</template>

<script>
    import Vue from 'vue'//vue

    export default {
        name: "Login",
        data(){
            return{
                //
                checked: false,
                //
                imgUrl:"",
                //
                account:"",
                password:"",
                verification:"",
                //
                show: false,
                //
                text:"",
                //
                user:""
            }
        },
        methods:{
            onClickLeft() {
                this.$router.back(-1);
            },
        //    
            changeImg(){
                Vue.axios.post("https://elm.cangdu.org/v1/captchas").then((result)=>{
                    this.imgUrl=result.data.code
                }).catch((error)=>{
                    console.log(error);
                })
            },
        //    
            judge(){
                Vue.axios.post("https://elm.cangdu.org/v2/login",{username:this.account,password:this.password,captcha_code:this.verification}).then((result)=>{
                    console.log(result.data);
                    this.user=result.data.message;
                    console.log(this.user);
                    if (this.user=="") {
                        this.show=true;
                        this.text="//"
                        this.changeImg();
                    }else if (this.user=="") {
                        this.show=true;
                        this.text=""
                        this.changeImg();
                    }else if (this.user==""){
                        this.show=true;
                        this.text=""
                        this.changeImg();
                    } else if (this.user==""){
                        this.show=true;
                        this.text=""
                        this.changeImg();
                    }else if (this.user=="") {
                        this.show=true;
                        this.text=""
                        this.changeImg();
                    }else{
                        console.log(result.data);
                        localStorage.setItem('user',JSON.stringify(result.data));
                        this.$router.push({path:'/order1/profile'});
                        //vuex
                        this.$store.commit("ZYMpublic_mutations", result.data);
                    }
                }).catch((error)=>{
                    console.log(error);
                })
            }
        },
    //    dom
        mounted(){
            Vue.axios.post("https://elm.cangdu.org/v1/captchas").then((result)=>{
                this.imgUrl=result.data.code
            }).catch((error)=>{
                console.log(error);
            })
        }
    }
</script>

<style scoped>
/**/
    .three{
        width: 100%;
        height: 15rem;
        background: white;
        margin-top: 1.5rem;
    }
    /**/
    .three_one{
        width: 100%;
        height: 5rem;
        border-bottom: 0.1rem solid #cdcdcd;
        border-top: 0.1rem solid rgba(0,0,0,0);
    }
    .three_one:nth-child(3){
        border-bottom: none;
        position: relative;
    }
    /**/
    .three_one input{
        width: 100%;
        height: 3rem;
        font-size: 1.7rem;
        padding: 0rem 1.5rem;
        margin: 1rem 0rem;
        border: none;
    }
    .three_one:nth-child(2) input{
        width: 82%;
    }
    .three_one:nth-child(3) input{
        width: 60%;
    }
    .three_one img{
        height: 3rem;
    }
    /**/
    .van-switch{
        width: 4rem;
        height: 2rem;
        top: 0.5rem;
        background: #cdcdcd;
    }
    /**/
    .van-switch div{
        width: 3rem;
        height: 3rem;
        top: -0.5rem;
        left: -1rem;
    }
    /**/
    .ming_close{
        font-size: 1.1rem;
        color: white;
        position: absolute;
        right: 3rem;
        top: 12.5rem;
    }
    /**/
    .imgurl_click{
        position: absolute;
        display: inline-block;
        width: 5rem;
        height: 4rem;
        font-size: 1.3rem;
        margin-top: 0.5rem;
        margin-left: 0.5rem;
    }
    .imgurl_click p{
        line-height: 2rem;
        display: inline-block;
        margin-bottom: 0rem;
    }
    .changone{
        color: cornflowerblue;
    }
    /**/
    .warm_prompt{
        width: 100%;
        line-height: 3rem;
        padding: 0rem 1.5rem;
    }
    .warm_prompt p{
        margin-bottom: 0rem;
        font-size: 1.3rem;
        color: red;
    }
    /**/
    .but{
        width: 100%;
        padding: 0rem 1.3rem;
    }
    .but .btn{
        background: #4cd964;
        height: 5rem;
        font-size: 2rem;
        color: white;
    }
    /**/
    .chongzhi{
        margin: 2rem;
        text-decoration: none;
    }
</style>


