<template>
    <div class="profileinfo">
        <!---->
        <div class="detail_top"><van-nav-bar title="" left-arrow @click-left="onClickLeft"></van-nav-bar></div>
        <!---->
        <van-cell title="" is-link @click="onPickFile">
            <img src="//elm.cangdu.org/img/default.jpg" class="user_header">
        </van-cell>
        <input type="file" ref="fileInput" accept="image/*" style="display: none" @change="getFile">
        <van-cell title="" is-link :to="{path:'/profile/info/setusername'}"><span>{{username}}</span></van-cell>
        <van-cell title="" is-link :to="{path:'/profile/info/address'}"></van-cell>
        <!---->
        <p></p>
        <van-cell is-link @click="goApp">
            <template slot="title">
                <i class="iconfont">&#xe68d;</i>
                <span class="custom-title"></span>
            </template>
        </van-cell>
        <!---->
        <p></p>
        <van-cell title="" is-link :to="{path:'/forget'}"><span></span></van-cell>
        <!---->
        <button type="button" class="btn btn-danger btn-block" @click="quitDl"></button>
        <!---->
        <transition enter-active-class="bounceIn 0.5s linear">
            <div v-show="show" class="tanchuang">
                <van-icon name="warning-o" />
                <br>
                <span>{{text}}</span>
                <button @click="show=!show"></button>
            </div>
        </transition>
        <!---->
        <transition enter-active-class="bounceIn 0.5s linear">
            <div v-show="showTwo" class="tanchuang onetc">
                <van-icon name="warning-o" />
                <br>
                <span></span>
                <br>
                <button @click="showTwo=!showTwo" class="oneClick"></button>
                <button @click="twoClick" class="twoClick"></button>
            </div>
        </transition>
    </div>
</template>

<script>
    import Vue from 'vue'//vue
    export default {
        name: "ProfileInfo",
        data(){
            return{
                show:false,
                text:"",
                //
                showTwo:false,
            }
        },
        methods:{
            onClickLeft() {
                this.$router.push({path:"/order1/profile"});

            },
            //
            onPickFile () {
                this.$refs.fileInput.click()
            },
            //
            getFile () {
                this.show=true;
                this.text="";
            },
            //App
            goApp(){
                this.show=true;
                this.text="APP";
            },
            //
            quitDl(){
                this.showTwo=!this.showTwo;
            },
            //
            twoClick(){
                Vue.axios.get("https://elm.cangdu.org/admin/singout").then((result)=>{
                    console.log(result);
                    if (result.data.message="") {
                        localStorage.removeItem("user");
                        //vuex
                        this.$store.commit("ZYMpublic_mutations", {});
                        this.$router.push({path:"/order1/profile"});
                    }
                }).catch((error)=>{
                    console.log(error);
                })
            }
        },
        //
        computed:{
            username() {
                return this.$store.state.p.ZYMpublic.username;
            }
        },
    }
</script>

<style scoped>
    .user_header{
        width: 5rem;
        height: 5rem;
        border-radius: 50%;
    }
    /**/
    .van-cell{
        width: 100%;
        height: 5rem;
        font-size: 1.5rem;
        padding: 1rem 2rem;
    }
    .van-cell i{
        font-size: 1.5rem;
        color: #9d9d9d;
    }
    .van-cell i,.van-cell span{
        line-height: 3rem;
    }
    /**/
    .van-cell i{
        font-size: 1.5rem;
        color: #9d9d9d;
    }
    .van-cell:nth-child(2){
        margin-top: 1rem;
        height: 7rem;
    }
    .van-cell:nth-child(2) span,.van-cell:nth-child(2) i{
        line-height: 5rem;
    }
    /**/
    p{
        width: 100%;
        height: 3rem;
        line-height: 3rem;
        font-size: 1.2rem;
        padding: 0rem 2rem;
        margin-bottom: 0rem;
    }
    /**/
    .van-cell:nth-child(7) div i{
        font-size: 2rem;
        line-height: 3rem;
        color: #10aeff;
        vertical-align: top;
    }
    /**/
    .btn{
        width: 95%;
        margin: 2rem auto;
        height: 4rem;
        font-size: 1.5rem;
    }
    /**/
    .onetc{
        width: 80%;
        left: 4rem;
    }
    .onetc i{
        font-size: 10rem;
    }
    .onetc span{
        font-size: 3rem;
        margin-bottom: 4rem;
    }
    .onetc button{
        border-radius: 0.5rem;
        width: 10rem;
        height: 4rem;
        margin: 0rem 1rem 2rem;
        font-size: 1.7rem;
    }
    .oneClick{
        background: #c1c1c1!important;
    }
    .twoClick{
        background: #dd6b55;
    }
</style>


