<template>
    <div class="vipcard">
        <!---->
        <div class="detail_top"><van-nav-bar title="" left-arrow @click-left="onClickLeft"></van-nav-bar></div>
        <!--xxx-->
        <p class="purchase"><span>{{users.username}}</span></p>
        <!---->
        <div class="privilege">
            <div @click="vipDescription">
                <span></span>
                <span class="pull-right"><van-icon name="arrow" /></span>
            </div>
            <div class="twoT">
                <img :src="imgUrl1" alt="">
                <div class="two_text">
                    <p></p>
                    <p>3034</p>
                    <p></p>
                </div>
            </div>
            <div class="twoT">
                <img :src="imgUrl2" alt="">
                <div class="two_text">
                    <p></p>
                    <p>3034</p>
                    <p></p>
                </div>
            </div>
        </div>
        <!---->
        <div class="privilege">
            <div><span></span></div>
            <div class="goumai">
                <span>1<span class="red_text">20</span></span>
                <button class="pull-right" @click="ToBuy"></button>
            </div>
        </div>
        <!---->
        <div class="privilege" @click="ToUseCart">
            <div>
                <span></span>
                <span class="pull-right"><van-icon name="arrow" /></span>
            </div>
        </div>
        <!---->
        <div class="privilege" @click="ToInvoiceRecord">
            <div>
                <span></span>
                <span class="pull-right"><van-icon name="arrow" /></span>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "Vipcard",
        data(){
            return{
                imgUrl1:"data:image/jpeg;base64,",
                imgUrl2:"data:image/jpeg;base64,"
            }
        },
        //
        methods:{
            onClickLeft() {
                this.$router.push({path:"/order1/profile"});
            },
            vipDescription(){
                this.$router.push({path:"/vipDescription"});
            },
            ToBuy(){
                this.$router.push({path:"/confirmOrder/payment"});
            },
            ToUseCart(){
                this.$router.push({path:"/vipcard/useCart"});
            },
            ToInvoiceRecord(){
                this.$router.push({path:"/vipcard/invoiceRecord"});
            }
        },
        //
        computed:{
            users(){
                return this.$store.state.p.ZYMpublic
            },
        },
        created(){
            console.log(this.users);
        }
    }
</script>

<style scoped>
.purchase{
    width: 100%;
    font-size: 1.5rem;
    padding: 1.5rem;
}
.purchase span{
    font-weight: bold;
}
/**/
.privilege{
    width: 100%;
    background: white;
    padding-left: 1.5rem;
}
.privilege div{
    padding-right: 1.5rem;
    border-bottom: 0.01rem solid #ddd;
}
.privilege div:nth-child(1) span:nth-child(1){
    font-size: 1.7rem;
    line-height: 5rem;
}
.privilege div:nth-child(1) span:nth-child(2){
    font-size: 1.3rem;
    color: #999;
}
.privilege div:nth-child(1) span:nth-child(2) i{
    font-size: 1.5rem;
    line-height: 5rem;
    vertical-align: -0.3rem;
}
.privilege div img{
    width: 11%;
    vertical-align: 1rem;
}
.two_text{
    display: inline-block;
    width: 85%;
    font-size: 1.7rem;
    border-bottom: none!important;
    padding-left: 0.5rem;
}
.two_text p:nth-child(1){
    font-size: 1.7rem;
    line-height: 2rem;
    color: #333;
}
.two_text p{
    font-size: 1.1rem;
    line-height: 1.5rem;
    color: #999;
}
.twoT{
    padding-top: 2rem;
    padding-bottom: 2rem;
    border-bottom: 0.01rem solid #ddd;
}
.twoT:nth-child(3){
    border-bottom: none;
}
/**/
.privilege:nth-child(4){
    margin-top: 1.5rem;
}
.goumai{
    font-size: 1.8rem;
    border-bottom: none!important;
}
.goumai span{
    line-height: 5rem;
}
.goumai button{
    font-size: 1.4rem;
    line-height: 2.5rem;
    width: 7rem;
    border: 0.1rem solid #ff6600;
    color: #ff6600;
    background: none;
    border-radius: 0.4rem;
    margin-top: 1rem;
}
/**/
.privilege:nth-child(5){
    margin: 1.5rem 0rem;
}
</style>


