<template>
    <div class="reset">
        <!---->
        <div class="detail_top"><van-nav-bar title="" left-arrow @click-left="onClickLeft"></van-nav-bar></div>
        <!---->
        <div class="three">
            <div class="three_one">
                <input type="text" placeholder="" v-model="account">
            </div>
            <div class="three_one">
                <input type="text" placeholder="" v-model="oldPassword">
            </div>
            <div class="three_one">
                <input type="text" placeholder="" v-model="newPassword">
            </div>
            <div class="three_one">
                <input type="text" placeholder="" v-model="confirmpassword">
            </div>
            <div class="three_one">
                <input type="text" placeholder="" v-model="verification">
                <img :src="imgUrl">
                <span class="imgurl_click" @click="changeImg">
                    <p></p>
                    <p class="changone"></p>
                </span>
            </div>
        </div>

        <!---->
        <div class="but">
            <button type="button" class="btn btn-lg btn-block" @click="changePass"></button>
        </div>

        <!---->
        <transition enter-active-class="bounceIn 0.5s linear">
            <div v-show="show" class="tanchuang">
                <van-icon name="warning-o" />
                <br>
                <span>{{text}}</span>
                <button @click="show=!show"></button>
            </div>
        </transition>
    </div>
</template>

<script>

    import Vue from 'vue'//vue
    import { Dialog } from 'vant';

    export default {
        name: "Reset",
        data(){
            return{
                //    
                imgUrl:"",
                //    
                account:"",
                oldPassword:"",
                newPassword:"",
                confirmpassword:"",
                verification:"",
                //
                show: false,
                //    
                text:"",
            //    
                allData:""
            }
        },
        methods:{
            onClickLeft() {
                this.$router.back(-1);
            },
            //    
            changeImg(){
                Vue.axios.post("https://elm.cangdu.org/v1/captchas").then((result)=>{
                    this.imgUrl=result.data.code
                }).catch((error)=>{
                    console.log(error);
                })
            },
        //    
            changePass(){
                Vue.axios.post("https://elm.cangdu.org/v2/changepassword",{username:this.account,oldpassWord:this.oldPassword,newpassword:this.newPassword,confirmpassword:this.confirmpassword,captcha_code:this.verification}).then((result)=>{
                    console.log(result.data);
                    this.allData=result.data.message;
                    if (this.allData=="") {
                        this.show=true;
                        this.text=""
                    }else if (this.allData==""){
                        this.show=true;
                        this.text=""
                    }else if (this.allData=="") {
                        this.show=true;
                        this.text=""
                    }else if (this.allData=="") {
                        this.show=true;
                        this.text=""
                    }else if (this.allData=="") {
                        this.show=true;
                        this.text=""
                    }else if (this.allData=="") {
                        this.show=true;
                        this.text=""
                        this.changeImg();
                    } else if (this.allData=="") {
                        this.show=true;
                        this.text=""
                    }else if (this.allData=="") {
                        this.show=true;
                        this.text=""
                    }else if (this.allData=="") {
                        this.show=true;
                        this.text=""
                    }else if(result.data.success==""){
                        this.show=true;
                        this.text=""
                    }
                }).catch((error)=>{
                    console.log(error);
                })
            }
        },
        //    dom
        mounted(){
            Vue.axios.post("https://elm.cangdu.org/v1/captchas").then((result)=>{
                this.imgUrl=result.data.code
            }).catch((error)=>{
                console.log(error);
            })
        }
    }
</script>

<style scoped>
    /**/
    .three{
        width: 100%;
        height: 25rem;
        background: white;
        margin-top: 1.5rem;
    }
    /**/
    .three_one{
        width: 100%;
        height: 5rem;
        border-bottom: 0.1rem solid #cdcdcd;
        border-top: 0.1rem solid rgba(0,0,0,0);
    }
    .three_one:nth-child(5){
        border-bottom: none;
        position: relative;
    }
    /**/
    .three_one input{
        width: 100%;
        height: 3rem;
        font-size: 1.7rem;
        padding: 0rem 1.5rem;
        margin: 1rem 0rem;
        border: none;
    }
    .three_one:nth-child(5) input{
        width: 60%;
    }
    .three_one img{
        height: 3rem;
    }
    /**/
    .imgurl_click{
        position: absolute;
        display: inline-block;
        width: 5rem;
        height: 4rem;
        font-size: 1.3rem;
        margin-top: 0.5rem;
        margin-left: 0.5rem;
    }
    .imgurl_click p{
        line-height: 2rem;
        display: inline-block;
        margin-bottom: 0rem;
    }
    .changone{
        color: cornflowerblue;
    }
    /**/
    .but{
        margin-top: 2.5rem;
        width: 100%;
        padding: 0rem 1.3rem;
    }
    .but .btn{
        background: #4cd964;
        height: 5rem;
        font-size: 2rem;
        color: white;
    }
</style>


