<template>
    <div class="exchange">
        <!---->
        <div class="detail_top"><van-nav-bar title="" left-arrow @click-left="onClickLeft"></van-nav-bar></div>
        <div class="input_all">
            <!---->
            <input type="text" placeholder="" class="oneInput" v-model="redeem_code" @input="judge" onkeyup="value=value.replace(/[^0-9]/g,'')">
            <!---->
            <input type="text" placeholder="" class="twoInput pull-left" v-model="yzm" @input="judge" maxlength='4'>
            <!---->
            <div class="yzmimg pull-right">
                <img :src="imgUrl" class="pull-left">
                <span class="imgurl_click" @click="changeimg">
                    <p></p>
                    <p class="changone"></p>
                </span>
            </div>
            <div class="clearfix"></div>
            <!---->
            <button class="conversion" :disabled="isDis" @click="duihuan"></button>
        </div>

        <!---->
        <transition enter-active-class="bounceIn 0.5s linear">
            <div v-show="show" class="tanchuang">
                <van-icon name="warning-o" />
                <br>
                <span>{{text}}</span>
                <button @click="show=!show"></button>
            </div>
        </transition>
    </div>
</template>

<script>
    import Vue from "vue"
    export default {
        name: "EXchange",
        data(){
            return{
                imgUrl:"",
                //
                isDis:true,
                //
                redeem_code:"",
                //
                yzm:"",
                //
                allUser:{},
                //
                show: false,
                //    
                text:"",
            }
        },
        //
        // computed(){
        //
        // },
        //
        methods: {
            onClickLeft() {
                this.$router.push({path:"/benefit"});
            },
            //
            changeimg(){
                Vue.axios.post("https://elm.cangdu.org/v1/captchas").then((result)=>{
                    this.imgUrl=result.data.code
                }).catch((error)=>{
                    console.log(error);
                })
            },
            //
            judge(){
                if (this.redeem_code.replace(/\D/g,'')&&this.yzm.length==4){
                    this.isDis=false;
                }else{
                    this.isDis=true;
                }
            },
            //
            duihuan(){
                Vue.axios.post(`https://elm.cangdu.org/v1/users/${this.allUser.user_id}/hongbao/exchange`,{exchange_code:this.redeem_code,captcha_code:this.yzm}).then((result)=>{
                    if(result.data.message==""){
                        this.show=true;
                        this.text=""
                    }
                }).catch((error)=>{
                    console.log(error);
                })
            }
        },
        created(){
            this.allUser=this.$store.state.p.ZYMpublic;
            Vue.axios.post("https://elm.cangdu.org/v1/captchas").then((result)=>{
                this.imgUrl=result.data.code
            }).catch((error)=>{
                console.log(error);
            })
        }
    }
</script>

<style scoped>
.input_all{
    text-align: center;
    padding: 1.5rem;
}
/**/
input{
    height: 5rem;
    background: white;
    border: none;
    border-radius: 0.5rem;
    padding-left: 1.2rem;
    font-size: 1.6rem;
}
/**/
.oneInput{
    width: 100%;
    margin-bottom: 1.5rem;
}
/**/
.twoInput{
    width: 57%;
}
/**/
.yzmimg{
    width: 40%;
    background: white;
    height: 5rem;
    border-radius: 0.5rem;
    padding: 0.5rem;
}
/**/
.yzmimg img{
    height: 4rem;
    width: 8rem;
}
.imgurl_click p{
    font-size: 1.2rem;
    line-height: 2rem;
}
.changone{
    color: cornflowerblue;
}
/**/
.conversion{
    width: 100%;
    height: 5rem;
    background: #4cd964;
    font-size: 1.8rem;
    line-height: 5rem;
    color: white;
    border: none;
    border-radius: 0.5rem;
    margin-top: 1.5rem;
}
    button:disabled{
        background: #cccccc;
    }
</style>


