<template>
    <div class="AddressAdd">
        <!---->
        <div class="detail_top"><van-nav-bar title="" left-arrow @click-left="onClickLeft"></van-nav-bar></div>
        <!---->
        <div class="particulars">
            <input type="text" placeholder="" v-model="name" @input="redOne" :class="{inputRed:isInputOne}">
            <p v-show="isInputOne"></p>
            <input type="text" placeholder="//" v-model="address" @click="toAddDetail">
            <input type="text" placeholder="" v-model="address_detail" @input="redTwo" :class="{inputRed:isInputTwo}">
            <p v-show="isInputTwo">{{songadd}}</p>
            <input type="text" placeholder="" v-model="phone" @input="redThree" :class="{inputRed:isInputThree}">
            <p v-show="isInputThree">{{songPhone}}</p>
            <input type="text" placeholder=" ()" v-model="phone_bk" @input="redFour" :class="{inputRed:isInputFour}">
            <p v-show="isInputFour"></p>
        </div>
        <!---->
        <div class="success">
            <button @click="AddRess"></button>
        </div>
        <!---->
        <transition enter-active-class="bounceIn 0.5s linear">
            <div v-show="show" class="tanchuang">
                <van-icon name="warning-o" />
                <br>
                <span>{{text}}</span>
                <button @click="show=!show"></button>
            </div>
        </transition>
    </div>
</template>

<script>
    import Vue from 'vue'//vue
    export default {
        name: "AddressAdd",
        data(){
            return{
                text:"",
                show:false,
                //
                dataMessage:"",
                //
                name:"",
                address:"",
                address_detail:"",
                phone:"",
                phone_bk:"",
                oneA:{},
                //
                oneAddress:{},
                //
                songadd:"",
                //
                songPhone:"",
                //
                isInputOne:false,
                isInputTwo:false,
                isInputThree:false,
                isInputFour:false,
            }
        },
        //
        computed: {
            username() {
                return this.$store.state.p.ZYMpublic
            },
            AddUser(){
                return this.$store.state.p.ZYMcity
            }
        },
        //
        methods: {
            onClickLeft() {
                this.$router.push({path: "/profile/info/address"});
                localStorage.removeItem("oneUserAdd");
            },
            //
            toAddDetail(){
                this.$router.push({name:"detail",params:{add:this.address}});
                localStorage.setItem('oneUserAdd',JSON.stringify({name:this.name,address_detail:this.address_detail,phone:this.phone,phone_bk:this.phone_bk}));
            },
            AddRess(){
                console.log(this.username);
                //
                Vue.axios.post(`https://elm.cangdu.org/v1/users/${this.username.user_id}/addresses`,{user_id:this.username.user_id,address:this.address,address_detail:this.address_detail,geohash:this.oneAddress.geohash,name:this.name,phone:this.phone,tag:4,sex:1,phone_bk:this.phone_bk,tag_type:4}).then((result)=>{
                    this.dataMessage=result.data.message;
                    console.log(this.dataMessage);
                    if (this.dataMessage==""){
                        this.show=true;
                        this.text=""
                    }else if (this.dataMessage=="") {
                        this.show=true;
                        this.text=""
                    }else if (this.dataMessage==""){
                        this.show=true;
                        this.text=""
                    }else{
                        this.show=true;
                        this.text=""
                        // console.log("");
                    }
                }).catch((error)=>{
                    console.log(error);
                })
            },
            //
            redOne(){
                if (this.name==""){
                    this.isInputOne=true;
                }else{
                    this.isInputOne=false;
                }
            },
            redTwo(){
                if (this.address_detail.length==0){
                    this.songadd="";
                    this.isInputTwo=true;
                }else if (this.address_detail.length<3){
                    this.songadd=",";
                    this.isInputTwo=true;
                }else{
                    this.isInputTwo=false;
                }
            },
            redThree(){
                if (this.phone.length==0){
                    this.songPhone="";
                    this.isInputThree=true;
                } else{
                    this.songPhone="";
                    this.isInputThree=true;
                }

            },
            redFour(){
                if (this.phone_bk==""){
                    this.isInputFour=false;
                }else{
                    this.isInputFour=true;
                }
            }
        },
        created(){
            this.oneAddress=this.$route.params;
            console.log(this.oneAddress.geohash);
            this.address=this.oneAddress.name;
            this.oneA=JSON.parse(localStorage.getItem('oneUserAdd')) || {};
            this.name=this.oneA.name;
            this.address_detail=this.oneA.address_detail;
            this.phone=this.oneA.phone;
            this.phone_bk=this.oneA.phone_bk;
        }
    }
</script>

<style scoped>
.particulars{
    width: 100%;
    background: white;
    margin-top: 1rem;
    padding: 1rem;
}
.particulars input{
    width: 100%;
    height: 3.5rem;
    margin-bottom: 1rem;
    border: 0.1rem solid #d9d9d9;
    border-radius: 0.3rem;
    background: #f2f2f2;
    padding-left: 1rem;
    font-size: 1.4rem;
}
.success{
    width: 100%;
    padding: 1rem;
}
button{
    width: 100%;
    height: 3.5rem;
    background: #4cd964;
    border: none;
    border-radius: 0.3rem;
    font-size: 1.5rem;
    color: white;
}
/**/
p{
    font-size: 1rem;
    color: red;
    margin-bottom: 1rem;
}
p:nth-child(9){
    margin-bottom: 0rem;
}
.inputRed{
    border: 0.1rem solid red!important;
}
</style>


